.dice {
  width: 200px;
  height: 200px;
  background-color: #c2c2c2;
  display: inline-block;
  margin: 0 30px;
  border: 6px solid #151515;
  border-radius: 10%;
}

.flexcenter {
  display: flex;
  justify-content: center;
}

.circle {
  background-color: #151515;
  width: 42px;
  height: 42px;
  border-radius: 50%;
  margin: 10px;
}

.one {
  grid-column-start: 1;
  grid-row-start: 1;
}

.two {
  grid-column-start: 2;
  grid-row-start: 1;
}

.three {
  grid-column-start: 3;
  grid-row-start: 1;
}

.four {
  grid-column-start: 1;
  grid-row-start: 2;
}

.five {
  grid-column-start: 2;
  grid-row-start: 2;
}

.six {
  grid-column-start: 3;
  grid-row-start: 2;
}

.seven {
  grid-column-start: 1;
  grid-row-start: 3;
}

.eight {
  grid-column-start: 2;
  grid-row-start: 3;
}

.nine {
  grid-column-start: 3;
  grid-row-start: 3;
}

.grid {
  display: grid;
  width: 180px;
  height: 180px;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 200px;
}
